* {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box
}

*:before,
  *:after {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

html,
body{
  height: 100%;
}

html #root,body #root{
    height: 100%;
  }

html #root .wrapper,body #root .wrapper{
      height: 100%;
    }


